import Vue from 'vue'
import Vuex from 'vuex'
import { MANAGER_ABI, MANAGER_ADDRESS } from "@/util/constants.js";

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    account_id: null,
    listings: null,
    my_listings: null,
    my_bookings: null,
  },
  mutations: {
    update_id(state, id) {
      state.account_id = id;
    },
    update_listings(state, listings) {
      state.listings = listings;
    },
    update_my_listings(state, listings) {
      state.my_listings = listings;
    },
    update_my_bookings(state, bookings) {
      state.my_bookings = bookings;
    }
  },
  actions: {
    update_listings({ commit, state }) {
      const manager_contract = new window.web3.eth.Contract(
        MANAGER_ABI,
        MANAGER_ADDRESS,
        {
          from: state.account_id,
          gasPrice: "10000000" // Could modify this but it works
        }
      );
      manager_contract.methods
        .getListings()
        .call({ from: state.account_id })
        .then(listings => {
          commit("update_listings", JSON.parse(listings))
        })
        .then(() => { // update my listings
          let l = []
          state.listings.forEach(elem => {
            if (elem.renter.toLowerCase() == state.account_id.toLowerCase()) {
              l.push(elem)
            }
          });
          commit("update_my_listings", l)
        })
        .then(() => { // update my bookings
          // manager_contract.methods.getRenteeListings(state.account_id).call({from: state.account_id})
          // .then((bookings) => {
          //   console.log("renteeListings: ", bookings);  
          // })
          let my_bookings = []
          for (let i = 0; i < state.listings.length; i++) {
            for (let j = 0; j < state.listings[i].bookings.length; j++) {
              let booking = state.listings[i].bookings[j];
              //let d = new Date();
              //let curr_time = d.getTime();
              // add boolean && curr_time >= booking.startDate && curr_time <= booking.endDate
              if (booking.rentee.toLowerCase() == state.account_id.toLowerCase()) {
                my_bookings.push(state.listings[i].bookings[j]);
              }
            }
          }
          commit("update_my_bookings", my_bookings);
        });
    }
  },
  modules: {
  }
})
